.header {
  background: url("bg-pattern-intro-mobile.85d10c50.svg") no-repeat center center, linear-gradient(to right bottom, var(--gradient-light-red), var(--gradient-red) );
  height: max(80vh, 570px);
  border-bottom-left-radius: 100px;
}

.header__navbar {
  justify-content: space-between;
  align-items: center;
  padding: 48px 24px 0;
  display: flex;
}

.navbar__trigger {
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 4px;
  padding: 4px;
  transition: box-shadow .3s;
}

.navbar__trigger:focus {
  box-shadow: var(--focus-shadow);
  outline: none;
}

.header__content {
  width: 100%;
  min-height: calc(100% - 88px);
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.header__title {
  font-family: var(--title-font);
  color: var(--white);
  text-align: center;
  font-size: 2rem;
  font-weight: 500;
}

.header__description {
  font-family: var(--body-font);
  color: var(--white);
  text-align: center;
  margin: 24px 0;
  font-size: 1.2rem;
  font-weight: 300;
}

.header__row {
  justify-content: center;
  gap: 16px;
  display: flex;
}

.header__link {
  font-family: var(--title-font);
  border: 1px solid var(--white);
  text-align: center;
  border-radius: 30px;
  padding: 16px;
  font-size: 1.25rem;
  font-weight: 700;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.header__link:hover {
  filter: brightness(85%);
}

.header__link:focus {
  box-shadow: var(--focus-shadow);
  outline: none;
}

.header__link:active {
  opacity: .5;
}

.header__link--start {
  background-color: var(--white);
  color: var(--light-red);
}

.header__link--learn {
  color: var(--white);
}

.introduction {
  flex-direction: column;
  align-items: center;
  margin-top: 56px;
  display: flex;
}

.introduction > * {
  margin-bottom: 32px;
}

.introduction__illustration {
  width: 100%;
}

.infra {
  width: 100%;
  background: url("bg-pattern-circles.d5778d88.svg") no-repeat bottom, linear-gradient(to right bottom, var(--gradient-dark-blue), var(--gradient-blue) );
  border-radius: 0 100px;
  margin-top: 156px;
  padding: 120px 24px 64px;
  position: relative;
}

.infra__phones {
  width: 100%;
  position: absolute;
  top: 24px;
  left: 0;
  transform: translate(0, -50%);
}

.infra__title {
  color: var(--white);
  margin-top: 96px;
  font-size: 2.5rem;
}

.infra__text {
  color: var(--white);
}

.advantages {
  margin-top: 48px;
}

.advantages > * {
  margin-bottom: 32px;
}

.advantages__laptop {
  width: 100%;
}

.footer {
  background-color: var(--darker-black-blue);
  border-top-right-radius: 100px;
  flex-direction: column;
  align-items: center;
  padding: 32px 16px 40px;
  display: flex;
}

.footer__title {
  font-family: var(--title-font);
  color: var(--white);
  margin-bottom: 40px;
  font-size: 2.5rem;
  font-weight: 700;
}

.footer__subtitle {
  color: var(--white);
  font-family: var(--title-font);
  margin-bottom: 24px;
  font-size: 1.25rem;
}

.footer__links {
  text-align: center;
  margin-bottom: 24px;
  list-style: none;
}

.footer__links > :not(:last-of-type) {
  margin-bottom: 8px;
}

.footer__link-item {
  color: var(--greyish-blue);
  font-family: var(--body-font);
  font-size: 1.25rem;
  font-weight: 300;
  text-decoration: none;
}

.footer__link-item:hover {
  text-decoration: underline;
}

.footer__link-item:focus {
  outline: 1px solid var(--white);
  opacity: .5;
}

:-webkit-any(.footer__title, .footer__subtitle, .footer__link-item)::-webkit-selection {
  background-color: var(--light-red);
}

:is(.footer__title, .footer__subtitle, .footer__link-item)::selection {
  background-color: var(--light-red);
}

